.breed-select {
  width: 250px;
  color: #333;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-bottom: 25px;
  padding: 10px;
  font-size: 16px;
  transition: border-color .3s, box-shadow .3s;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .1);
}

.breed-select:hover {
  border-color: #007bff;
  box-shadow: 0 2px 10px rgba(0, 123, 255, .5);
}

.breed-select:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 0 5px rgba(0, 123, 255, .5);
}

.cat-info {
  align-items: flex-start;
  gap: 20px;
  display: flex;
}

.cat-info img {
  width: auto;
  height: auto;
  max-width: 400px;
  max-height: 350px;
}

.cat-info-text {
  max-width: 600px;
  flex-direction: column;
  display: flex;
}

.cat-info-name {
  margin: 0 0 20px;
  font-size: 30px;
}

.cat-info-description {
  margin: 0 0 20px;
  font-size: 18px;
}

.cat-info-temperament {
  font-size: 18px;
}

.is-hidden {
  display: none;
}

.loader {
  color: #fff;
  background-color: #007bff;
  border-radius: 5px;
  margin-bottom: 15px;
  padding: 15px 25px;
  font-size: 20px;
  transition: opacity .3s;
  box-shadow: 0 4px 10px rgba(0, 0, 0, .2);
}

.error {
  color: #fff;
  background-color: #dc3545;
  border-radius: 5px;
  padding: 15px 25px;
  font-size: 18px;
  transition: opacity .3s;
  box-shadow: 0 4px 10px rgba(0, 0, 0, .2);
}

